* {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: bold;
}

ol, ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

.wrap {
  display: none;
}

.fadein {
  display: block;
  animation: fadein_wrap 0.7s forwards;
  animation-delay: 0.1s !important;
  opacity: 0;
}

@keyframes fadein_wrap {
  0% {
    -webkit-transform: translate3d(0, 1000px, 0);
    transform: translate3d(0, 1000px, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.fadeout {
  display: block;
  animation: fadeout .7s forwards;
}

@keyframes fadeout {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
}

.intro {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  display: none;
}

.intro .bgL {
  float: left;
  width: 50%;
  height: 100%;
  background-color: #3e3e3e;
}

.intro .bgR {
  float: right;
  width: 50%;
  height: 100%;
  background-color: #3e3e3e;
}

.intro .main_tit {
  position: absolute;
  top: 38%;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  color: #fff;
  font-size: 150px;
  animation: trackingIn 1s forwards;
  animation-delay: !important;
  opacity: 0;
}

@keyframes trackingIn {
  0% {
    letter-spacing: -0.5em;
    transform: translate(0, 0);
    opacity: 0;
  }
  100% {
    transform: translate(0, 0);
    opacity: 1;
  }
}

.bg_ani_open {
  animation: bg .8s forwards;
}

.bg_ani_close {
  animation: bg2 .8s forwards;
}

@keyframes bg {
  0% {
    width: 50%;
  }
  100% {
    width: 0%;
  }
}

@keyframes bg2 {
  0% {
    width: 0%;
  }
  100% {
    width: 50%;
  }
}

header {
  width: 100%;
  height: 50px;
  background-color: #3e3e3e;
}

header nav {
  width: 35%;
  height: 100%;
  margin-left: 30px;
}

header nav ul li {
  float: left;
  padding-top: 10px;
}

header nav ul li a {
  display: block;
  height: 30px;
  color: #fff;
  padding: 0 15px;
  line-height: 25px;
  border-radius: 2px;
}

header nav ul li.active a {
  background-color: #ff6060;
}

.menu {
  width: 100%;
  height: 134px;
  border-bottom: 1px solid #dbdbdb;
  overflow: hidden;
}

.menu ul {
  display: none;
}

.menu ul li {
  float: left;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background-color: #3e3e3e;
  text-align: center;
  cursor: pointer;
  margin: 13px 20px 0;
}

.menu ul li.active {
  background-color: #ff6060;
}

.menu ul li a {
  color: #fff;
  padding-top: 45px;
  display: block;
}

.menu ul.active {
  display: block !important;
}

.unfold_bounce_li a {
  padding-top: 37px !important;
}

.on {
