}

.sample_box__tit {
  margin-top: 250px;
  text-align: center;
  font-size: 100px;
  font-weight: bold;
  color: #3e3e3e;
  animation: flash 1.5s infinite;
  animation-delay: !important;
  display: none;
}

@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}

